<template>
  <v-app>
    <v-content> 
      <dark-template-container>
        <v-snakbar v-model="installDialog" vertical color="#7c2a95" :timeout="installDialogTimeout">
          <p>Would you want install the app?</p>
          <br>
          <v-btn color="#6ccf9e" dark block rounded @click="istallApp()">Yes, Install it!</v-btn>
          <v-btn color="#6ccf9e" text block rounded @click="disMissInstall()">No</v-btn>

        </v-snakbar>
      </dark-template-container>
    </v-content>
  </v-app>
</template>

<script>
import DarkTemplateContainer from '@/views/dark-template/Container'

export default {
  name      : 'App',
  components: { DarkTemplateContainer },
  data () {
    istallDialog: false;
    istallDialogTimeout: 10000;
    deferredPromed: null;
     
  },
  methods:{
    installApp(){
      this.istallDialog = false;
      this.deferredPromed.prompt();
      this.deferredPromed.userChoice.then(choiceResult=>{
        if(choiceResult.outcome === "accepted"){
          this.istallDialog = false
        }else{
          this.istallDialog = false
        }
          this.istallDialog = false
      });
    },
    disMissInstall(){
      this.istallDialog = false
    }
  },
  mounted(){
    window.addEventListener('beforeistallprompt' , (e)=>{
      e.preventDefault();
      this.deferredPromed = e;
        if(this.isMobileCheck) {
          this.istallDialog = true
        }
    });
  }
}
</script>
